/* Szary- 58, 59, 61
Czerwony- 240, 50, 50
Szary2-71, 67, 67
Biały-250, 245, 245 */

.Footer{
    margin-left: 30px;
    margin-right: 30px;
    border-top: 2px solid rgb(240, 50, 50) ;
    border-left: 2px solid rgb(240, 50, 50) ;
    border-right: 2px solid rgb(240, 50, 50);
    border-top-left-radius: 20px;
    border-top-right-radius: 20px ;

    height: 80px;
    padding-top: 20px;
    padding-left:20px;
    
    display: flex;
    justify-content: space-between;
    align-items: center;
}



@media screen and (max-width: 1000px){
    .Footer{
        padding: 0;
        margin:0;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }

}