/* Szary- 58, 59, 61
Czerwony- 240, 50, 50
Szary2-71, 67, 67
Biały-250, 245, 245 */

.nav{

    margin-left: 30px;
    margin-right: 30px;
    border-bottom: 2px solid rgb(240, 50, 50) ;
    border-left: 2px solid rgb(240, 50, 50) ;;
    border-right: 2px solid rgb(240, 50, 50) ;;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px ;
    padding: 10px;
    display: flex;
    align-items: center ;
    justify-content: space-between ;

    }
    .menuIcon{
    color:white;
    font-size: 40px;
    margin-left: 70px;
    margin-top: 8px;
    display: none;
}
    .navlinks{
        margin: 10px;
        display: flex;
        justify-content: space-between;
    }
    .logo{
        margin-left: 40px;
        padding: 10px;
        display: flex;
    }

@media screen and (max-width: 1000px) {
    .nav{
        margin:0;
        
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
    }
    .menuIcon{
        display: block;
    }
    .navlinks {
        display:none;
    }

    .navlinksactive{
        display: block;
        width: 80%;
    }
    button{
        width: 100%;
    }
}