.btn {
    font-family: 'Varela Round', sans-serif;
    font-weight:500;
    border:none;
    cursor: pointer;
    transition: 0.1s ease-out;
    text-transform: uppercase;
    text-decoration: none;
    outline: none;
}
button:active, button:focus {
    outline: 0;
    outline-style: none;
  }
/* btn col & styles */

.btn--primary--full {
    color: white;
    border: 2px solid rgb(240, 50, 50);
    border-radius: 8px;
    background-color:rgb(58, 59, 61) ;
    box-shadow:  0 0 2px red;
    transition: all 0.3s ease-out;
}
.btn--primary--full:hover{
 background-color: rgb(240, 50, 50);
}

.active{
    color: white;
    border: 2px solid rgb(240, 50, 50);
    border-radius: 8px;
    background-color:rgb(240,50,50) ;
    box-shadow:  0 0 2px red;
}
/* btn size  */
.btn--medium{
    padding: 5px 15px;
    margin: 10px;
    font-size: 20px;
}

.Main{
    margin: 20px 30px ;
    display: grid;
    grid-template-columns:15% auto ;
    grid-template-rows:800px 160px auto;

}

p{
    color:rgb(250, 245, 245);
    font-weight: 600;
    font-size: 16px;
    font-weight: 400;
    margin: 0;
    padding: 0;
}
.ofertyp{
    padding: 20px;
}

.oferty{
grid-row-start: 1;
grid-row-end:3;
border-radius: 20px;
border:2px solid rgb(240, 50, 50) ;

display: flex;
flex-wrap: wrap;
justify-content: center;
align-content:flex-start ;


}
.socialmedia{
    height: 100px;
    width: 100%;
    margin-bottom: 20px;
    border-bottom: rgb(240, 50, 50) solid 2px;
    color: wheat;

    display: flex;
    justify-content: center;
    align-items: center;
}


.maincontent{
grid-column-start: 2;
grid-column-end: 2;
grid-row-start: 1;
grid-row-end:3;


}

@media screen and (max-width: 1000px) { 
    .Main{
        margin: 0px;
        padding: 0px;
        grid-template-rows: 800px 200px;
        margin-top: 10px;
    }
    .oferty{
        grid-column-start: 1;
        grid-column-end: 3 ;
        grid-row-start: 1;
        grid-row-end:2;
    }
    .maincontent{
        grid-column-start: 1;
        grid-column-end: 3 ;
        grid-row-start: 2;
        grid-row-end:2;
    }
}
.image-wrapper {
    position: relative;
  }
  .image-hover {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transition: opacity 0.2s ease-out;
  }
  .image-hover:hover {
    opacity: 1;
  }
