.maincontact{
    margin: 20px 30px ;
    display: grid;
    grid-template-columns:20% auto 20%;
    grid-template-rows:960px;
}
.contactcomp{
    grid-column-start:2 ;
    grid-column-end:2 ;
    border: 2px solid rgb(240, 50, 50) ;
    border-radius: 20px;
}

@media screen and (max-width: 1000px) { 
    .maincontact{
        margin: 10px;
    }
    .contactcomp{
        grid-column-start:1 ;
        grid-column-end:-1 ;
    }
    .Footer{

    }
}