
.Projectblocks{
    padding: 30px 20px;

    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.Projects{
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: auto ;
    grid-template-rows: 100px auto 100px ;
}

@media screen and (max-width: 1000px){

    .Projects{
        grid-template-rows: auto auto auto;

    }
    .block{
        width: 400px;
    }

    .Projectblocks{
        padding: 30px 0px;
    }
}