/* Szary- 58, 59, 61
Czerwony- 240, 50, 50
Szary2-71, 67, 67
Biały-250, 245, 245 */
@import url('https://fonts.googleapis.com/css2?family=Varela+Round&display=swap');

.btn {
    font-family: 'Varela Round', sans-serif;
    font-weight:500;
    border:none;
    cursor: pointer;
    transition: 0.1s ease-out;
    text-transform: uppercase;
    text-decoration: none;
    outline: none;
}
button:active, button:focus {
    outline: 0;
    outline-style: none;
  }
/* btn col & styles */

.btn--primary--full {
    color: white;
    border: 2px solid rgb(240, 50, 50);
    border-radius: 8px;
    background-color:rgb(58, 59, 61) ;
    box-shadow:  0 0 2px red;
    transition: all 0.3s ease-out;
}
.btn--primary--full:hover{
 background-color: rgb(240, 50, 50);
}

.active{
    color: white;
    border: 2px solid rgb(240, 50, 50);
    border-radius: 8px;
    background-color:rgb(240,50,50) ;
    box-shadow:  0 0 2px red;
}
/* btn size  */
.btn--medium{
    padding: 5px 15px;
    margin: 10px;
    font-size: 20px;
}
