.block{
    margin: 10px;
    border: 2px solid rgb(240, 50, 50) ;
    border-radius: 20px ;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width:500px;
    height: auto;
    transition: all 0.5s;
}

.project-display{
    border:none;
    border-top-left-radius: 17px;
    border-top-right-radius: 17px;
    width: 100%;
    height: 250px;   

}
.description{
    border-top: rgb(240, 50, 50) solid 2px;
    width: 100%;
    height: 50px;
}


