.Main{
    margin: 20px 30px ;
    display: grid;
    grid-template-columns:15% auto ;
    grid-template-rows:800px 160px auto;

}

p{
    color:rgb(250, 245, 245);
    font-weight: 600;
    font-size: 16px;
    font-weight: 400;
    margin: 0;
    padding: 0;
}
.ofertyp{
    padding: 20px;
}

.oferty{
grid-row-start: 1;
grid-row-end:3;
border-radius: 20px;
border:2px solid rgb(240, 50, 50) ;

display: flex;
flex-wrap: wrap;
justify-content: center;
align-content:flex-start ;


}
.socialmedia{
    height: 100px;
    width: 100%;
    margin-bottom: 20px;
    border-bottom: rgb(240, 50, 50) solid 2px;
    color: wheat;

    display: flex;
    justify-content: center;
    align-items: center;
}


.maincontent{
grid-column-start: 2;
grid-column-end: 2;
grid-row-start: 1;
grid-row-end:3;


}

@media screen and (max-width: 1000px) { 
    .Main{
        margin: 0px;
        padding: 0px;
        grid-template-rows: 800px 200px;
        margin-top: 10px;
    }
    .oferty{
        grid-column-start: 1;
        grid-column-end: 3 ;
        grid-row-start: 1;
        grid-row-end:2;
    }
    .maincontent{
        grid-column-start: 1;
        grid-column-end: 3 ;
        grid-row-start: 2;
        grid-row-end:2;
    }
}